  footer {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* .section-heading::after{
      display: inline-block;
      content: "";
      width: 9rem;
  } */
  .section-heading::before {
    counter-increment: sectionHeadingCounter;
    content: counter(sectionHeadingCounter, decimal-leading-zero) ". ";
    color: var(--fontSecondary);
    font-size: 0.5em;
  }

  #sayHelloLink{
    padding: 1em 1em;
    color: inherit;
    font-size: 1em;
    text-decoration: none;
    background-color: var(--lightBackgroundPrimary);
    border: 1px solid var(--fontSecondary);
    border-radius: 5px;
  }
  #sayHelloLink:hover{
      background-color: var(--backgroundPrimary);
  }
  